.complete_form {
    margin: 10px;
    padding: 10% 20%;
    background-color: #fff;
    box-shadow: 0 2px 10px rgba(154, 154, 154, 0.315);
    border-radius: 10px;
}
.complete_form i {
    font-size: 30px;
    padding: 20px 30px;
    border-radius: 40px;
    color: greenyellow;
    margin-left: 36%;
    box-shadow: 0 2px 10px rgba(154, 154, 154, 0.315);
}
.complete_form h1 {
    font-size: 40px;
    color: darkblue;
}